"use client";
import React from "react";
import { Renderer, type FormSchema } from "@shadcn-builder/renderer";
import { useDesignerStore } from "./state/store";

export function Canvas({ registry, viewport }: { registry: any; viewport: "sm" | "md" | "lg" }) {
  const schema = useDesignerStore((s) => s.schema);
  const setSchema = useDesignerStore((s) => s.setSchema);
  const selectedId = useDesignerStore((s) => s.selectedId);
  const select = useDesignerStore((s) => s.select);

  const move = (index: number, delta: number) => {
    const arr = [...schema.components];
    const to = index + delta;
    if (to < 0 || to >= arr.length) return;
    const [item] = arr.splice(index, 1);
    arr.splice(to, 0, item);
    setSchema({ ...schema, components: arr });
  };

  return (
    <div>
      <div className="mb-2 text-sm text-slate-500">点击选中组件，右侧可编辑属性；上下箭头可重排。</div>
      <div className="space-y-2">
        {schema.components.map((n, i) => (
          <div key={n.id} className={`border rounded p-2 ${selectedId === n.id ? "ring-2 ring-blue-500" : ""}`}>
            <div className="flex items-center gap-2 mb-2">
              <button className="border px-1 rounded" onClick={() => move(i, -1)}>↑</button>
              <button className="border px-1 rounded" onClick={() => move(i, 1)}>↓</button>
              <button className="ml-auto text-xs underline" onClick={() => select(n.id)}>选择</button>
            </div>
            <Renderer schema={{ components: [n] } as FormSchema} registry={registry} context={{ viewport }} />
          </div>
        ))}
      </div>
    </div>
  );
}
